.resets() {
  .geometry-reset() {
    margin: 0;
    padding: 0;
  }
  .text-reset() {
    font-weight: normal;
  }
  .button-reset() {
    border: 0;
    background: transparent;
    font: inherit;
  }
  .select-reset() {
    border: 0;
    font: inherit;
    appearance: none;
    background-color: transparent;
  }
  .input-reset() {
    border: 0;
    font: inherit;
  }
}

.select-arrow() {
  background-image: url('../images/select-arrow.svg');
  background-position: right;
  background-repeat: no-repeat;
  transition: background-image .3s;
  &:hover,
  &:active,
  &:focus {
    background-image: url('../images/select-arrow-hover.svg');
  }
}

.square-box(@size) {
  width: @size;
  height: @size;
}

//icons in project boxes
.project-box-icons(@n) when (@n > 0) {
  .project-box-icons((@n - 1));
  &:nth-of-type(@{n}) .box__heading::before {
    background-image: url('../images/project-box-icon-@{n}.svg');
  }
}

//animation duration for project boxes appearing
.animation-dur (@n) when (@n > 0) {
  .animation-dur((@n - 1));
  &:nth-of-type(@{n}) {
    animation-duration: ((@n * 1s) - .5);
  }
}
